
@import url('https://fonts.googleapis.com/css2?family=Nunito:wght@400;600;700;800;900&display=swap');


* {font-family: 'Nunito', sans-serif;}

:hover {transition: .3s ease-in-out all;}

body {background: #e4f4f8}

.title {width: 100%;padding: 30px 0;text-align: center;}
.title h1 {font-size: 2.3rem;font-weight: 600;margin: 0;}
.breadcrumb {background: none;text-align: center;justify-content: center;padding: 0;margin-bottom: 0}

header {background: #363939;margin-bottom: 25px;padding: 10px 0;}
header .container-fluid {padding: 0;}
header .navbar-brand {background: #363939;padding: 10px;}
header .navbar-brand img {max-width: 210px;}
header .navbar.navbar-light {padding: 0;}
header .navbar-light .navbar-nav .nav-link.addhtl {background: #ebb330;color: #fff;border-radius: 50px;}
header .navbar-light .navbar-nav .nav-link.addhtl:hover {color: #fff;}
header .navbar-light .navbar-nav .nav-link {font-size: 15px;line-height: 28px;text-transform: uppercase;color: #fff;padding: 5px 15px;font-weight: 600;}
header .navbar-light .navbar-nav .nav-link:hover {background: #ebb330;color: #fff;border-radius: 50px;}
header .navbar-expand-lg .navbar-nav li.nav-item {margin: 0 10px;}
header .navbar-nav .dropdown-menu {right: 0;left: inherit;}
header .navbar-nav .dropdown-menu .dropdown-item:hover {color: #fff;text-decoration: none;background-color: #ebb330;}
header .navbar-light .navbar-toggler {color: rgba(0,0,0,.5);border-color: #f2b81c;padding: 10px;}


#list-details {width: 100%;background: #fff;}
#list-details .table tr th {font-size: 18px;text-align: center;vertical-align: middle;color: #514e4e;}
#list-details .table tr td {font-size: 16px;text-align: center;vertical-align: middle;color: #626262;}
#list-details .table tr td .pr_box {padding: 4px 7px;color: #fff;border-radius: 3px;font-size: 13px;}
#list-details .table tr td .hot_img {max-width: 120px;height: 60px;}
#list-details .table tr th {background: #e6e6e6;}

#addJob {width: 100%;padding: 30px;background: #fff;}

#searhbar {max-width: 500px;margin: 15px auto;}
#searhbar .btn-reset {color: #fff;background-color: #6c928f;border-color: #6c928f;}

.sidebar-background {background-image: none;background-color: #363939;background-size: cover;}

.sidebar .sidebar-brand img {max-width: 170px;}
.sidebar-dark .sidebar-brand {color: #fff;padding: 5px;}

#searchForm {width: 100%;padding: 25px;padding-bottom: 0;}

.wrapper {
  display: flex;
}