
@import url('https://fonts.googleapis.com/css2?family=Work+Sans:wght@400;500;600;700&display=swap');

body {font-family: 'Work Sans', sans-serif;}

#login-block form.login-block {
    max-width: 500px;
    padding: 40px 35px;
    background: #fff;
    border-radius: 10px;
    margin: 50px auto;
    text-align: center;
}
#login-block form.login-block h3 {
	font-size: 26px;
	font-weight: bold;
	text-transform: uppercase;
	margin-bottom: 25px;
}
#login-block form.login-block img {text-align: center;margin-bottom: 15px;}
#login-block form.login-block .input-group {margin-bottom: 20px;}

.navbar-light .navbar-brand {
	color: #fff;
	font-weight: 700;
	text-transform: uppercase;
	font-size: 20px;
	letter-spacing: 1px;
}

header {
    background: #25378b;
}
.form-control {
	font-size: 18px;
}
.navbar-light .navbar-nav .nav-item {padding-right: 15px;padding-left: 15px;}
