.form-block {
	width: 100%;
	padding: 40px;
	background: #fff;
	margin: 50px 0;
	border-radius: 10px;
	box-shadow: 0 0 5px rgba(0,0,0,.2);
}
.form-block h1 {
    font-size: 30px;
    font-weight: 600;
    padding: 0 0 15px;
    margin: 0
}
.form-block .input-group {
    margin-bottom: 20px;
}
footer {
	position: fixed;
	bottom: 0;
	background: #e3e1e1;
	width: 100%;
	padding: 20px 0;
	text-align: center;
}
footer p {margin:0}