/*# sourceMappingURL=bootstrap.min.css.map */
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


* {font-family: 'Nunito', sans-serif;}
* {transition: all .5s ease;}
* {
	font-size: 15px;
	line-height: 27px;
}
ul {margin:0;padding:0;list-style: none;}
a {text-decoration: none;}
h1,h2,h3,h4,h5,h6 {font-weight: 700;}
.user-avatar {
	display: inline-block;
	border-radius: 50%;
	width: 46px;
	height: 46px;
	position: relative;
}
.navbar.navbar-expand-lg.navbar-light.bg-light {
	position: relative;
	z-index: 99999;
	width: 100%;
}
.red-text {color: #dc3139;}
.user-avatar img {
	width: 100%;
	display: inline-block;
	border-radius: 50%;
	object-fit: cover;
	border: 3px solid rgba(0, 0, 0, 0.08);
	image-rendering: -webkit-optimize-contrast;
}
.user-profile .user-name {
	font-weight: 600;
	color: #666;
	line-height: 20px;
	padding: 2px 0px 0 3px;
	display: inline-block;
	font-size: 16px;
}
.navbar-light .navbar-nav .nav-link {
	color: #666;
	padding: 15px 20px;
	font-size: 17px;
}
.navbar-light .navbar-nav .nav-link:hover {
	color: #ee8024;
}
.navbar-light .navbar-nav .nav-link.user-profile {
	padding:8px 0 0;
}
.user-avatar::after {
	position: absolute;
	content: "";
	height: 12px;
	width: 12px;
	background-color: #c0c0c0;
	top: 0px;
	right: 0px;
	display: block;
	border: 2px solid #fff;
	box-shadow: 0 2px 3px rgba(0,0,0,0.3);
	border-radius: 50%;
}
.status-online::after {
	background-color: #25c848;
}
.pd-main {padding:80px 0;}
.bg-main {
	background: url(/static/media/bg-main.d3dea5a2.jpg) no-repeat 0 0/cover fixed;
	padding-top: 0;
}
.navbar-light .navbar-brand img {
	max-width: 200px;
}
#breadcrumb {
	position: absolute;
	right: 15px;
	display: inline-block;
	font-size: 14.7px;
	top: 50%;
	-webkit-transform: translateY(-51%);
	        transform: translateY(-51%);
	font-weight: 600;
	border-radius: 4px;
	color: #ffffff;
}
.dashboard-headline h3 {
	display: block;
	font-size: 26px;
	color: #ffffff;
	font-weight: 600;
}
.dashboard-headline {
	display: block;
	position: relative;
	margin-bottom: 0px;
	padding: 60px 0;
}
#breadcrumb ul {
	margin: 0;
	padding: 0;
	line-height: 24px;
}
#breadcrumb ul li {
	display: inline-block;
	list-style: none;
	margin: 0 0 0 7px;
}
#breadcrumb ul li:first-child {
	margin-left: 0;
}
.breadcrumb-item a {
	color: #333;text-decoration: none;
}
.breadcrumb-item.active {
	color: #fd8607;
}
.breadcrumb-item + .breadcrumb-item::before {
	color: #333;
}
.dashboard-box {
	display: block;
	border-radius: 4px;
	background-color: #fff;
	box-shadow: 0 2px 8px rgba(0,0,0,0.08);
	margin-bottom: 30px;
	position: relative;
}
.dashboard-box .headline {
	background: #424242;
	display: block;
	padding: 20px 25px;
	position: relative;
	border-radius: 4px 4px 0 0;
}
.dashboard-box .headline h3 {
	font-size: 18px;
	font-weight: 600;
	color: #ffffff;
	line-height: 26px;
}
.dashboard-box .content.with-padding {
	padding: 30px;
}
.submit-field h5 {
	font-size: 16px;
	font-weight: 600;
	color: #333;
	margin-bottom: 12px;
	text-align: left;
}
input.input-border, input[type="text"].input-border, input[type="password"].input-border, input[type="email"].input-border, input[type="number"].input-border, textarea.input-border, select.input-border {
	background: #fbfdff;
	border: 1px solid #dde6ef;
	box-shadow: 0 1px 4px 0px rgba(0, 0, 0, 0.05);
}
input, input[type="text"], input[type="password"], input[type="email"], input[type="number"], select {
    height: 48px;
	line-height: 48px;
	padding: 0 15px;
	outline: none;
	font-size: 16px;
	color: #808080;
	margin: 0 0 16px 0;
	max-width: 100%;
	width: 100%;
	box-sizing: border-box;
	display: block;
	background-color: #fbfdff;
	font-weight: 500;
	opacity: 1;
	border-radius: 4px;
	border: none;
	box-shadow: 0 1px 4px 0px rgba(0, 0, 0, 0.12);
}
textarea {
	line-height: 48px;
	padding: 0 15px;
	outline: none;
	font-size: 16px;
	color: #808080;
	margin: 0 0 16px 0;
	max-width: 100%;
	width: 100%;
	box-sizing: border-box;
	display: block;
	background-color: #fbfdff;
	font-weight: 500;
	opacity: 1;
	border-radius: 4px;
	border: none;
	box-shadow: 0 1px 4px 0px rgba(0, 0, 0, 0.12);
}
button[type="submit"] {
    background-color: #ff8a00;
    padding:13px 22px;
    font-size: 18px;
    border:none;
    color: #fff;
    border-radius: 4px;
}
.apply {
    background-color: #ff8a00;
    padding:13px 22px;
    font-size: 18px;
    border:none;
    color: #fff;
    border-radius: 4px;
}
#drop-region {
	background-color: #f2f2f2;
	border-radius: 4px;
	box-shadow: 0 0 35px rgba(0,0,0,0.05);
	width: 100%;
	padding: 60px 40px;
	text-align: center;
	cursor: pointer;
	transition: .3s;
	margin-bottom: 20px;
}
#drop-region:hover {
	box-shadow:0 0 45px rgba(0,0,0,0.1);
}

#image-preview {
	margin-top:20px;
}
#image-preview .image-view {
    display: inline-block;
	position:relative;
	margin-right: 13px;
	margin-bottom: 13px;
}
#image-preview .image-view img {
    max-width: 100px;
    max-height: 100px;
}
#image-preview .overlay {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    right: 0;
    z-index: 2;
    background: rgba(255,255,255,0.5);
}
.job-list .job-listing {
	box-shadow: none;
	margin-bottom: 15px;
	padding: 25px 30px;
	padding-right: 60px;
	display: block;
	width: 100%;
	float: left;
	border: 1px solid rgba(0, 0, 0, 0.06);
	border-radius: 6px;
	box-shadow: 0 1px 8px rgba(0,0,0,0.05);
	z-index: 1;
    background-color: #fff;
    transition: 0.3s;
    position: relative;
}
.job-listing .job-listing-details {
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	padding: 0;
	top: 0;
}
.job-listing .job-listing-description {
	flex: 1 1;
	padding-top: 0px;
	margin-top: -2px;
}
.dashboard-status-button.green {
	background: #e0f5d7;
	color: #449626;
}
.job-status-item, .status-item {
	margin: 0 0 4px 0px;
}
.dashboard-status-button {
	color: #fff;
	border-radius: 4px;
	font-size: 14px;
	line-height: 21px;
	padding: 2px 10px;
	display: inline-block;
	position: relative;
	top: -2px;
	margin: 0 0 4px 8px;
	font-weight: 600;
}
.job-listing .job-listing-title {
	font-size: 18px;
	line-height: 28px;
	margin-bottom: 8px;
	font-weight: 600;
	color: #514e4e;
	text-decoration: none;
}
.job-listing .job-listing .job-listing-footer {
	background-color: transparent;
	padding: 0;
	margin-top: 3px;
    border-radius: 0 0 4px 4px;
    position: relative;
}
.job-listing .job-listing-footer ul {
	list-style: none;
	padding: 0;
	margin: 0;
}
.job-listing .job-listing-footer ul li {
	background: #ffffff;
	display: inline-block;
	margin-right: 6px;
	margin-bottom: 5px;
	color: #8c8c8c;
	font-size: 14px;
	font-weight: 600;
	padding: 0px 8px;
	border-radius: 4px;
	box-shadow: 0 2px 6px rgba(0,0,0,0.10);
}
.dashboard-status-button.yellow {
	background-color: #fbf6dd;
	color: #ff8a00;
}
.job-list .job-listing:hover {
	-webkit-transform: scale(1.01);
	        transform: scale(1.01);
	box-shadow: 0 1px 12px rgba(0,0,0,0.06);
	overflow: hidden;
	border: 1px solid #ff8a00 !important;
}
.job-sidebar {
	position: relative;
	padding: 30px 30px 30px;
	background: #F5F7FC;
	border-radius: 8px;
	margin-bottom: 40px;
}
.job-sidebar .job-sidebar-title {
	position: relative;
	font-size: 20px;
	line-height: 24px;
	color: #202124;
	margin-bottom: 30px;
}
.job-details {
	position: relative;
}
.job-details li {
	position: relative;
	padding-left: 50px;
	margin-bottom: 30px;
}
.job-details h5 {
	font-size: 16px;
	line-height: 22px;
	color: #202124;
}
.job-details span {
	font-size: 15px;
	line-height: 20px;
}
.job-details .icon {
	position: absolute;
	left: 0;
	top: 0;
	height: 30px;
	width: 30px;
	background-repeat: no-repeat;
	background-position: center;
	font-size: 30px;
	color: #fd8607;
}
.apply-btn {margin-top: 35px;}

.company-overview li {
	display: block;
	color: var(--titleColor);
	width: 100%;
	margin-bottom: 10px;
	font-size: 16px;
}
.company-overview li span {
	color: var(--bodyColor);
	float: right;
}

.company-wrapper {
	margin-top: 35px;
}
.company-wrapper-content {
	float: left;
	width: 100%;
	margin-top: 30px;
	transition: all 0.5s;
}
.company-wrapper-main {
	float: left;
	width: 100%;
	background: #ffffff;
	padding-left: 30px;
	padding-right: 30px;
	padding-top: 30px;
	padding-bottom: 30px;
	border: 1px solid #e9e9e9;
	border-bottom: 0;
	transition: all 0.5s;
}
.company-keyqord-main {
	float: left;
	width: 100%;
	padding-top: 20px;
	padding-bottom: 20px;
	padding-left: 30px;
	padding-right: 30px;
	border: 1px solid #e9e9e9;
	background: #f9f9f9;
	border-top: 0;
	transition: all 0.5s;
}
.company-img {
	float: left;
	width: 105px;
}
.company-right-content {
	float: left;
	width: calc(100% - 105px);
	padding-left: 30px;
	padding-top: 10px;
}
.company-right-content h4 a {
	font-size: 22px;
	color: #000000;
	font-weight: bold;
}
.company-right-content h4 {
	background: none;
	border: none;
	padding: 0;
	margin: 0;
}
.company-job-cont p {
	margin-bottom: 5px;
	padding-top: 10px;
}
.company-right-content p {
	font-size: 14px;
	color: #23c0e9;
	padding-top: 5px;
}
.company-right-content li:last-child {
	color: #797979;
	font-size: 16px;
}
.company-right-content li:first-child {
	margin-left: 0;
	color: #000000;
	font-size: 16px;
	font-weight: bold;
}
.company-right-btn-content ul {
	float: right;
	margin-top: 15px;
}
.company-right-btn-content li:last-child {
	margin-left: 50px;
	margin-top: 40px;
	float: none;
}
.company-right-btn-content li:last-child a {
	float: left;
	width: 100%;
	padding-right: 20px;
	padding-left: 20px;
	height: 30px;
	line-height: 30px;
	text-align: center;
	background: #ff8a00;
	color: #ffffff;
	font-size: 12px;
	text-transform: uppercase;
	border-radius: 10px;
}
.company-keyqord-main li:first-child {
	margin-left: 0;
	color: #000000;
	transition: all 0.5s;
}
.company-keyqord-main li i {
	padding-right: 5px;
	color: #23c0e9;
	transition: all 0.5s;
}
.company-keyqord-main li {
	float: left;
	margin-left: 20px;
}
h4 {
	font-size: 18px;
	background: #f5f6f7;
	padding: 8px 20px;
	margin-bottom: 16px;
	line-height: 1.6;
	border-radius: 2px;
	color: #252d40;
	border-left: 3px solid #fd8607;
}
.home-header {
	background-repeat: no-repeat;
