
@import url('bootstrap.min.css');
@import url('https://fonts.googleapis.com/css2?family=Nunito:wght@400;600;700;800;900&display=swap');

* {font-family: 'Nunito', sans-serif;}
* {transition: all .5s ease;}
* {
	font-size: 15px;
	line-height: 27px;
}
ul {margin:0;padding:0;list-style: none;}
a {text-decoration: none;}
h1,h2,h3,h4,h5,h6 {font-weight: 700;}
.user-avatar {
	display: inline-block;
	border-radius: 50%;
	width: 46px;
	height: 46px;
	position: relative;
}
.navbar.navbar-expand-lg.navbar-light.bg-light {
	position: relative;
	z-index: 99999;
	width: 100%;
}
.red-text {color: #dc3139;}
.user-avatar img {
	width: 100%;
	display: inline-block;
	border-radius: 50%;
	object-fit: cover;
	border: 3px solid rgba(0, 0, 0, 0.08);
	image-rendering: -webkit-optimize-contrast;
}
.user-profile .user-name {
	font-weight: 600;
	color: #666;
	line-height: 20px;
	padding: 2px 0px 0 3px;
	display: inline-block;
	font-size: 16px;
}
.navbar-light .navbar-nav .nav-link {
	color: #666;
	padding: 15px 20px;
	font-size: 17px;
}
.navbar-light .navbar-nav .nav-link:hover {
	color: #ee8024;
}
.navbar-light .navbar-nav .nav-link.user-profile {
	padding:8px 0 0;
}
.user-avatar::after {
	position: absolute;
	content: "";
	height: 12px;
	width: 12px;
	background-color: #c0c0c0;
	top: 0px;
	right: 0px;
	display: block;
	border: 2px solid #fff;
	box-shadow: 0 2px 3px rgba(0,0,0,0.3);
	border-radius: 50%;
}
.status-online::after {
	background-color: #25c848;
}
.pd-main {padding:80px 0;}
.bg-main {
	background: url(../imgs/bg-main.jpg) no-repeat 0 0/cover fixed;
	padding-top: 0;
}
.navbar-light .navbar-brand img {
	max-width: 200px;
}
#breadcrumb {
	position: absolute;
	right: 15px;
	display: inline-block;
	font-size: 14.7px;
	top: 50%;
	transform: translateY(-51%);
	font-weight: 600;
	border-radius: 4px;
	color: #ffffff;
}
.dashboard-headline h3 {
	display: block;
	font-size: 26px;
	color: #ffffff;
	font-weight: 600;
}
.dashboard-headline {
	display: block;
	position: relative;
	margin-bottom: 0px;
	padding: 60px 0;
}
#breadcrumb ul {
	margin: 0;
	padding: 0;
	line-height: 24px;
}
#breadcrumb ul li {
	display: inline-block;
	list-style: none;
	margin: 0 0 0 7px;
}
#breadcrumb ul li:first-child {
	margin-left: 0;
}
.breadcrumb-item a {
	color: #333;text-decoration: none;
}
.breadcrumb-item.active {
	color: #fd8607;
}
.breadcrumb-item + .breadcrumb-item::before {
	color: #333;
}
.dashboard-box {
	display: block;
	border-radius: 4px;
	background-color: #fff;
	box-shadow: 0 2px 8px rgba(0,0,0,0.08);
	margin-bottom: 30px;
	position: relative;
}
.dashboard-box .headline {
	background: #424242;
	display: block;
	padding: 20px 25px;
	position: relative;
	border-radius: 4px 4px 0 0;
}
.dashboard-box .headline h3 {
	font-size: 18px;
	font-weight: 600;
	color: #ffffff;
	line-height: 26px;
}
.dashboard-box .content.with-padding {
	padding: 30px;
}
.submit-field h5 {
	font-size: 16px;
	font-weight: 600;
	color: #333;
	margin-bottom: 12px;
	text-align: left;
}
input.input-border, input[type="text"].input-border, input[type="password"].input-border, input[type="email"].input-border, input[type="number"].input-border, textarea.input-border, select.input-border {
	background: #fbfdff;
	border: 1px solid #dde6ef;
	box-shadow: 0 1px 4px 0px rgba(0, 0, 0, 0.05);
}
input, input[type="text"], input[type="password"], input[type="email"], input[type="number"], select {
    height: 48px;
	line-height: 48px;
	padding: 0 15px;
	outline: none;
	font-size: 16px;
	color: #808080;
	margin: 0 0 16px 0;
	max-width: 100%;
	width: 100%;
	box-sizing: border-box;
	display: block;
	background-color: #fbfdff;
	font-weight: 500;
	opacity: 1;
	border-radius: 4px;
	border: none;
	box-shadow: 0 1px 4px 0px rgba(0, 0, 0, 0.12);
}
textarea {
	line-height: 48px;
	padding: 0 15px;
	outline: none;
	font-size: 16px;
	color: #808080;
	margin: 0 0 16px 0;
	max-width: 100%;
	width: 100%;
	box-sizing: border-box;
	display: block;
	background-color: #fbfdff;
	font-weight: 500;
	opacity: 1;
	border-radius: 4px;
	border: none;
	box-shadow: 0 1px 4px 0px rgba(0, 0, 0, 0.12);
}
button[type="submit"] {
    background-color: #ff8a00;
    padding:13px 22px;
    font-size: 18px;
    border:none;
    color: #fff;
    border-radius: 4px;
}
.apply {
    background-color: #ff8a00;
    padding:13px 22px;
    font-size: 18px;
    border:none;
    color: #fff;
    border-radius: 4px;
}
#drop-region {
	background-color: #f2f2f2;
	border-radius: 4px;
	box-shadow: 0 0 35px rgba(0,0,0,0.05);
	width: 100%;
	padding: 60px 40px;
	text-align: center;
	cursor: pointer;
	transition: .3s;
	margin-bottom: 20px;
}
#drop-region:hover {
	box-shadow:0 0 45px rgba(0,0,0,0.1);
}

#image-preview {
	margin-top:20px;
}
#image-preview .image-view {
    display: inline-block;
	position:relative;
	margin-right: 13px;
	margin-bottom: 13px;
}
#image-preview .image-view img {
    max-width: 100px;
    max-height: 100px;
}
#image-preview .overlay {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    right: 0;
    z-index: 2;
    background: rgba(255,255,255,0.5);
}
.job-list .job-listing {
	box-shadow: none;
	margin-bottom: 15px;
	padding: 25px 30px;
	padding-right: 60px;
	display: block;
	width: 100%;
	float: left;
	border: 1px solid rgba(0, 0, 0, 0.06);
	border-radius: 6px;
	box-shadow: 0 1px 8px rgba(0,0,0,0.05);
	z-index: 1;
    background-color: #fff;
    transition: 0.3s;
    position: relative;
}
.job-listing .job-listing-details {
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	padding: 0;
	top: 0;
}
.job-listing .job-listing-description {
	flex: 1;
	padding-top: 0px;
	margin-top: -2px;
}
.dashboard-status-button.green {
	background: #e0f5d7;
	color: #449626;
}
.job-status-item, .status-item {
	margin: 0 0 4px 0px;
}
.dashboard-status-button {
	color: #fff;
	border-radius: 4px;
	font-size: 14px;
	line-height: 21px;
	padding: 2px 10px;
	display: inline-block;
	position: relative;
	top: -2px;
	margin: 0 0 4px 8px;
	font-weight: 600;
}
.job-listing .job-listing-title {
	font-size: 18px;
	line-height: 28px;
	margin-bottom: 8px;
	font-weight: 600;
	color: #514e4e;
	text-decoration: none;
}
.job-listing .job-listing .job-listing-footer {
	background-color: transparent;
	padding: 0;
	margin-top: 3px;
    border-radius: 0 0 4px 4px;
    position: relative;
}
.job-listing .job-listing-footer ul {
	list-style: none;
	padding: 0;
	margin: 0;
}
.job-listing .job-listing-footer ul li {
	background: #ffffff;
	display: inline-block;
	margin-right: 6px;
	margin-bottom: 5px;
	color: #8c8c8c;
	font-size: 14px;
	font-weight: 600;
	padding: 0px 8px;
	border-radius: 4px;
	box-shadow: 0 2px 6px rgba(0,0,0,0.10);
}
.dashboard-status-button.yellow {
	background-color: #fbf6dd;
	color: #ff8a00;
}
.job-list .job-listing:hover {
	transform: scale(1.01);
	box-shadow: 0 1px 12px rgba(0,0,0,0.06);
	overflow: hidden;
	border: 1px solid #ff8a00 !important;
}
.job-sidebar {
	position: relative;
	padding: 30px 30px 30px;
	background: #F5F7FC;
	border-radius: 8px;
	margin-bottom: 40px;
}
.job-sidebar .job-sidebar-title {
	position: relative;
	font-size: 20px;
	line-height: 24px;
	color: #202124;
	margin-bottom: 30px;
}
.job-details {
	position: relative;
}
.job-details li {
	position: relative;
	padding-left: 50px;
	margin-bottom: 30px;
}
.job-details h5 {
	font-size: 16px;
	line-height: 22px;
	color: #202124;
}
.job-details span {
	font-size: 15px;
	line-height: 20px;
}
.job-details .icon {
	position: absolute;
	left: 0;
	top: 0;
	height: 30px;
	width: 30px;
	background-repeat: no-repeat;
	background-position: center;
	font-size: 30px;
	color: #fd8607;
}
.apply-btn {margin-top: 35px;}

.company-overview li {
	display: block;
	color: var(--titleColor);
	width: 100%;
	margin-bottom: 10px;
	font-size: 16px;
}
.company-overview li span {
	color: var(--bodyColor);
	float: right;
}

.company-wrapper {
	margin-top: 35px;
}
.company-wrapper-content {
	float: left;
	width: 100%;
	margin-top: 30px;
	-webkit-transition: all 0.5s;
	-o-transition: all 0.5s;
	-ms-transition: all 0.5s;
	-moz-transition: all 0.5s;
	transition: all 0.5s;
}
.company-wrapper-main {
	float: left;
	width: 100%;
	background: #ffffff;
	padding-left: 30px;
	padding-right: 30px;
	padding-top: 30px;
	padding-bottom: 30px;
	border: 1px solid #e9e9e9;
	border-bottom: 0;
	-webkit-transition: all 0.5s;
	-o-transition: all 0.5s;
	-ms-transition: all 0.5s;
	-moz-transition: all 0.5s;
	transition: all 0.5s;
}
.company-keyqord-main {
	float: left;
	width: 100%;
	padding-top: 20px;
	padding-bottom: 20px;
	padding-left: 30px;
	padding-right: 30px;
	border: 1px solid #e9e9e9;
	background: #f9f9f9;
	border-top: 0;
	-webkit-transition: all 0.5s;
	-o-transition: all 0.5s;
	-ms-transition: all 0.5s;
	-moz-transition: all 0.5s;
	transition: all 0.5s;
}
.company-img {
	float: left;
	width: 105px;
}
.company-right-content {
	float: left;
	width: calc(100% - 105px);
	padding-left: 30px;
	padding-top: 10px;
}
.company-right-content h4 a {
	font-size: 22px;
	color: #000000;
	font-weight: bold;
}
.company-right-content h4 {
	background: none;
	border: none;
	padding: 0;
	margin: 0;
}
.company-job-cont p {
	margin-bottom: 5px;
	padding-top: 10px;
}
.company-right-content p {
	font-size: 14px;
	color: #23c0e9;
	padding-top: 5px;
}
.company-right-content li:last-child {
	color: #797979;
	font-size: 16px;
}
.company-right-content li:first-child {
	margin-left: 0;
	color: #000000;
	font-size: 16px;
	font-weight: bold;
}
.company-right-btn-content ul {
	float: right;
	margin-top: 15px;
}
.company-right-btn-content li:last-child {
	margin-left: 50px;
	margin-top: 40px;
	float: none;
}
.company-right-btn-content li:last-child a {
	float: left;
	width: 100%;
	padding-right: 20px;
	padding-left: 20px;
	height: 30px;
	line-height: 30px;
	text-align: center;
	background: #ff8a00;
	color: #ffffff;
	font-size: 12px;
	text-transform: uppercase;
	-webkit-border-radius: 10px;
	-moz-border-radius: 10px;
	border-radius: 10px;
}
.company-keyqord-main li:first-child {
	margin-left: 0;
	color: #000000;
	-webkit-transition: all 0.5s;
	-o-transition: all 0.5s;
	-ms-transition: all 0.5s;
	-moz-transition: all 0.5s;
	transition: all 0.5s;
}
.company-keyqord-main li i {
	padding-right: 5px;
	color: #23c0e9;
	-webkit-transition: all 0.5s;
	-o-transition: all 0.5s;
	-ms-transition: all 0.5s;
	-moz-transition: all 0.5s;
	transition: all 0.5s;
}
.company-keyqord-main li {
	float: left;
	margin-left: 20px;
}
h4 {
	font-size: 18px;
	background: #f5f6f7;
	padding: 8px 20px;
	margin-bottom: 16px;
	line-height: 1.6;
	border-radius: 2px;
	color: #252d40;
	border-left: 3px solid #fd8607;
}
.home-header {
	background-repeat: no-repeat;
	background-position: center top;
	background-size: cover;
	background-attachment: fixed;
	position: relative;
	min-height: 650px;
	padding-top: 72px;
}
.home-header::before {
	position: absolute;
	content: '';
	background: #19365f;
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
	opacity: .7;
}
.banner-wording {
	z-index: 9999;
	position: relative;
	color: #fff;
	padding: 130px 0 0;
	text-align: center;
}
.banner-wording h1 {
	font-size: 50px;
	font-weight: 700;
}
.banner-wording h3 {
	font-size: 25px;
}
.banner-wording p {
	font-size: 20px;
}
.banner-wording .apply-btn .apply {
	display: inline;
}
.job-home-wrapper {
	position: relative;
	margin-bottom: 30px;
}
.job-home-wrapper .inner-box {
	position: relative;
	padding: 32px 20px 22px 30px;
	background: #FFFFFF;
	border: 1px solid #ECEDF2;
	-webkit-box-sizing: border-box;
	box-sizing: border-box;
	border-radius: 10px;
	-webkit-transition: all 300ms ease;
	-o-transition: all 300ms ease;
	transition: all 300ms ease;
}
.job-home-wrapper .content {
	position: relative;
	padding-left: 68px;
	min-height: 51px;
}
.job-home-wrapper .company-logo {
	position: absolute;
	left: 0;
	top: 0;
	width: 50px;
	-webkit-transition: all 300ms ease;
	-o-transition: all 300ms ease;
	transition: all 300ms ease;
}
.job-home-wrapper h4 {
	font-size: 18px;
	color: #202124;
	font-weight: 500;
	line-height: 26px;
	top: -3px;
	margin-bottom: 3px;
	border: none;
	background: none;
	padding: 0;
}

.job-home-wrapper h4 a {
	color: #202124;
	-webkit-transition: all 300ms ease;
	-o-transition: all 300ms ease;
	transition: all 300ms ease;
	font-size: 20px;
	font-weight: 600;
}
.job-home-wrapper .job-details {
	position: relative;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-wrap: wrap;
	flex-wrap: wrap;
	margin-bottom: 10px;
}
.job-home-wrapper .job-details li {
	position: relative;
	font-size: 14px;
	line-height: 22px;
	color: #696969;
	font-weight: 400;
	padding-left:0;
	margin-bottom: 5px;
	margin-right: 20px;
}
.job-home-wrapper .job-details li .icon {
	position: absolute;
	left: 0;
	top: 0;
	font-size: 18px;
	line-height: 22px;
	color: #696969;
}
.job-home-wrapper .job-detail-other {
	position: relative;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-wrap: wrap;
	flex-wrap: wrap;
}
.job-detail-other li.time {
	background: rgba(25,103,210,.15);
	color: #1967D2;
}
.job-detail-other li {
	position: relative;
	font-size: 13px;
	line-height: 15px;
	margin-right: 15px;
	padding: 5px 20px;
	border-radius: 50px;
	margin-bottom: 10px;
}
.job-detail-other li.green, .job-detail-other li.privacy {
	background: rgba(52,168,83,.15);
	color: #34A853;
}
.job-detail-other li.required {
	background: rgba(249,171,0,.15);
	color: #F9AB00;
}
.job-home-wrapper .company-logo img {
	max-width: 50px;
}
.light-bg {background-color: #f2f2f2;}

.job-box {
	background-repeat: no-repeat;
	background-position: center top;
	background-size: cover;
	background-attachment: fixed;
	position: relative;
	border: 10px solid #fff;
}
.job-box::before {
	position: absolute;
	content: '';
	background: #000;
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
	opacity: .7;
}
.job-box .job-inner {
	position: relative;
	z-index: 9999;
	max-width: 90%;
	margin: 50px auto;
	text-align: center;
	color: #fff;
}
.testimonial-box {
	background: #ffffff;
	box-shadow: 0 0 30px rgba(0,0,0,0.08);
	border-radius: 6px;
	padding: 45px 40px;
	color: #888;
	position: relative;
	margin: 70px 0 0;
	cursor: grab;
	text-align: center;
}
.testimonial-avatar {
	position: absolute;
	top: 0;
	transform: translateY(-50%);
	margin: 0 auto;
	left: 0;
	right: 0;
}
.testimonial-avatar img {
	width: 90px;
	display: inline-block;
	text-align: center;
	height: 90px;
	object-fit: cover;
	border-radius: 50%;
	margin: 0 auto;
	border: 4px solid rgba(255, 255, 255, 0.1);
	box-shadow: 0 5px 2px rgba(255, 138, 0,0.08);
}
.testimonial-name {
	display: block;
	width: 100%;
	padding: 20px 0 15px 0;
	text-align: center;
	margin: 0 auto;
}
.testimonial-name h4 {
	display: block;
	width: 100%;
	font-size: 18px;
	padding-top: 0;
	color: #333;
	font-weight: 600;
	background: none;
	padding: 0;
	border: none;
	margin: 0;
}
.title-section {
	text-align: center;
	margin-bottom: 40px;
}

@media screen and (max-width:767px) {
	.banner-wording h1 {
		font-size: 30px;
		font-weight: 700;
	}
	.banner-wording h3 {
		font-size: 25px;
	}
}


