	background-color: #fff !important;
}

footer {
	padding-top: 50px;
	padding-bottom: 50px;
	background-color: #202124;
}
footer .footer-logo {
	text-align: center;
}
footer .footer-logo img {
	border-radius: 10px;
}
.footer-menu {
	margin-top: 30px;
}
.footer-menu ul {
	text-align: center;
}
.footer-menu ul li {
	display: inline-block;
}
.footer-menu ul li a {
	color: #fff !important;
	font-size: 18px;
