@media (max-width: 575px) {
	.pd-main {
		padding-top: 40px;
		padding-bottom: 40px;
	}
	.dashboard-headline .row {
		flex-direction: column;
		text-align: center;
	}
	.dashboard-headline .row h3 {
		display: block;
		font-size: 32px;
		color: #fff;
		font-weight: 600;
		width: 100%;
	}
	.dashboard-headline .row #breadcrumb {
		position: relative;
		right: 0;
		display: inline-block;
		font-size: 14.7px;
		top: 0;
		transform: none;
		font-weight: 600;
		border-radius: 4px;
		color: #ffffff;
	}
	.dashboard-headline .row #breadcrumb ol {
		display: flex;
		flex-wrap: wrap;
		padding: 0 0;
		margin-bottom: 0;
		list-style: none;
	}
}

@media (max-width: 450px) {
	.browse-companies.php .row {
		display: block !important;
	}
	.browse-companies.php .company-right-content {
		float: none !important;
		width: 100% !important;
		display: inline-block !important;
		padding-left: 0 !important;
		margin-top: 20px !important;
	}
	.company-right-btn-content li:last-child a {
		height: auto !important;
	}
	.company-right-btn-content ul {
		float: none !important;
		margin-top: 15px !important;
	}
